import React from 'react'
import { useMemo, useState, useEffect, useRef, useContext } from 'react'

import {
  RenderSlide,
  RenderPresentation,
  PresentationContext
} from '../../Presentation'
import { RenderCite, RenderBibliography } from '../../Citations'

import { Show } from '../../animations'

// import sedes from './kuleuvensedes.svg'
import pointer from './pointer.png'
//
import './styles.css'

import step from '../../step.macro.js'
import { range } from '../../utils.js'

export function Presentation({ ...props }) {
  const h = 900
  const w = 1200
  return (
    <RenderPresentation
      {...props}
      renderSlide={({
        slides,
        slideIndex,
        stepIndex,
        info,
        numSlides,
        fullScreen
      }) => {
        const threeSlides = slides.slice(
          Math.max(0, slideIndex - 1),
          Math.min(numSlides, slideIndex + 2)
        )
        return (
          <div className='flex justify-center items-center bg-blue h-screen'>
            <div
              className='inline-block relative flex-shrink-0 flex flex-col text-white bg-blue'
              style={{
                overflow: 'hidden',
                fontFamily: 'Computer Modern Sans',
                width: w,
                height: h
                // border: '1px solid white'
              }}
            >
              <div className='flex-grow relative'>{threeSlides}</div>
              <div
                className='flex flex-col'
                style={{
                  transition: '0.5s transform',
                  transform: `translate(0px, ${info.hideNavigation ? 100 : 0}%)`
                }}
              >
                <div className='flex justify-between text-xs theme-font-open'>
                  <div className='px-2 py-1'>
                    {info.sectionSlide ? '\xa0' : info.section}
                  </div>
                  <div className='px-2 py-1'>{slideIndex + 1}</div>
                </div>
                <div className='w-full inline-block' style={{ height: 2 }}>
                  <div
                    className='h-full bg-green'
                    style={{
                      width: `${(slideIndex / (numSlides - 1)) * 100}%`,
                      transition: 'width 0.5s'
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        )
      }}
    />
  )
}

function MousePointer() {
  const [position, setPosition] = useState({ x: 0, y: 0 })
  const [hidden, setHidden] = useState(true)
  const currentTimer = useRef(null)
  useEffect(() => {
    const setFromEvent = (e) => {
      setHidden(false)
      clearTimeout(currentTimer.current)
      currentTimer.current = setTimeout(() => setHidden(true), 1000)
      setPosition({ x: e.clientX, y: e.clientY })
    }
    window.addEventListener('mousemove', setFromEvent)
    return () => {
      window.removeEventListener('mousemove', setFromEvent)
    }
  }, [])
  const { x, y } = position
  const width = 8
  const height = 8
  return (
    <img
      alt=''
      width={width}
      height={height}
      src={pointer}
      style={{
        display: hidden ? 'none' : 'inline',
        position: 'fixed',
        left: 0,
        top: 0,
        zIndex: 10,
        pointerEvents: 'none',
        transform: `translate(${x - width / 2}px, ${y - height / 2}px)`
      }}
    />
  )
}

export function Slide({ className, children, ...props }) {
  return (
    <RenderSlide
      children={children}
      {...props}
      render={({ info, content, slideIndex, i, style }) => {
        return (
          <div
            className='slide'
            style={{
              position: 'absolute',
              left: 0,
              top: 0,
              bottom: 0,
              right: 0,
              ...style
            }}
          >
            <div className='absolute inset-0 flex flex-col'>
              {info.header && (
                <div className='ml-6 mt-6 text-white font-semibold text-lg flex items-center theme-font-open'>
                  <span
                    style={{
                      height: '2em',
                      width: '4px',
                      marginRight: '1em',
                      background: '#00D56F',
                      borderRadius: '2px'
                    }}
                  />
                  {info.header}
                </div>
              )}
              <div className={`flex-grow p-6 flex flex-col ${className}`}>
                {content}
              </div>
            </div>
          </div>
        )
      }}
    />
  )
}

export function SectionSlide({ section }) {
  const { i, numSlides } = useContext(PresentationContext)
  return (
    <Slide
      className='flex justify-center items-center text-3xl theme-font-open'
      hideNavigation
    >
      <div>
        <div>{section}</div>
        <div
          className='mt-3'
          style={{
            background: 'rgba(255, 255, 255, 0.2)',
            width: '16em',
            height: '4px',
            borderRadius: '2px'
          }}
        >
          <div
            className='h-full bg-green'
            style={{
              width: `${(i / (numSlides - 1)) * 100}%`,
              borderRadius: '2px'
            }}
          />
        </div>
      </div>
    </Slide>
  )
}

